<template>
    <div class="qaforotc">
        <div class="item">
            <h4></h4>
            <ul class="clearfix">
                <li>
                    <a href="javascript:void(0);"></a>
                </li>
                <li>
                    <a href="javascript:void(0);">BI.CEO </a>
                </li>
                <li>
                    <a href="javascript:void(0);"></a>
                </li>
                <li>
                    <a href="javascript:void(0);">ETH</a>
                </li>
                <li>
                    <a href="javascript:void(0);">(</a>
                </li>
                <li>
                    <a href="javascript:void(0);"></a>
                </li>
            </ul>
        </div>
        <div class="item">
            <h4></h4>
            <ul class="clearfix">
                <li>
                    <a href="javascript:void(0);"></a>
                </li>
                <li>
                    <a href="javascript:void(0);"></a>
                </li>
                <li>
                    <a href="javascript:void(0);"></a>
                </li>
                <li>
                    <a href="javascript:void(0);"></a>
                </li>
                <li>
                    <a href="javascript:void(0);">BI.CEO </a>
                </li>
            </ul>
        </div>
        <div class="item">
            <h4></h4>
            <ul class="clearfix">
                <li>
                    <a href="javascript:void(0);"></a>
                </li>
                <li>
                    <a href="javascript:void(0);"></a>
                </li>
                <li>
                    <a href="javascript:void(0);"></a>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
    export default {
       name: 'QaforOtc' 
    }
</script>
<style lang="less" scoped>
    .qaforotc{
        padding: 30px 0;
        display: flex;

        .item{
            width: 33.33%;
            float: left;

            h4{
                font-size: 24px;
                font-weight: normal;
                position: relative;
                padding-left: 20px;
                margin-bottom: 20px; 
            }
            h4:before{
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                width: 8px;
                height: 24px;
                background: #5787f5;
                display: block;
                content: "";
                border-radius: 5px;
                margin: auto;
            }

            ul{
                background: rgba(255,255,255,0.1);
                padding:20px 30px;
                border-radius: 5px;
                min-height: 300px;

                li{
                    padding: 15px 0;

                    a{
                        position: relative;
                        padding-left: 20px;
                        font-size: 14px;
                    }
                    a:hover{
                        color:#fff
                    }
                    a:before{
                        position: absolute;
                        bottom: 0;
                        top: 0;
                        left: 0;
                        width: 8px;
                        height: 8px;
                        background: #20d6d7;
                        display: block;
                        content: "";
                        margin: auto
                    }
                }
            }
        }

        .item + .item{
            padding-left: 3%
        }
    }

    @media only screen and (min-width: 320px) and (max-width:768px){
        .qaforotc{
            display: block;
            padding: 0;
        }
        .qaforotc .item{
            float: none;
            width: 100%;
            padding: 0 15px;
            margin-bottom: 20px;
        }
        .qaforotc .item h4{
            margin-bottom: 10px;
        }
        .qaforotc .item ul{
            min-height: auto;
            padding: 0 15px;
        }
        .qaforotc .item ul li{
            padding: 10px 0;
        }

        .qaforotc .item + .item{
            padding-left: 15px;
        }
    }
</style>



