<template>
    <div class="publishpage">
        <div class="needlogin" v-if="!isuserlogin">
            <i class="el-icon-warning"></i>
            
        </div>
        <el-form ref="form" :model="form">
            <h1 class="title">
                
            </h1>
            <div class="publishtips">
                <p>BTC></p>
                <p></p>
                <p>BTC</p>
                <p>0.5%(1BTC0.005BTCOTB50%)</p>
                <p>140.025BTC</p>
            </div>
            <div class="pushitem">
                <div class="title"><b></b> ()</div>
                <div class="pushtype">
                    <el-select v-model="form.tradeType" placeholder="">
                        <el-option
                            v-for="item in tradeTypeOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                    <el-select v-model="form.coinTypeId" @change="CheckCoinType" placeholder="">
                        <el-option
                            v-for="item in coinTypeIdOptions"
                            :key="item.id"
                            :label="item.shortName"
                            :value="item.id">
                        </el-option>
                    </el-select>
                    <el-select v-model="form.countryId" placeholder="">
                        <el-option
                            v-for="item in countryIdOptions"
                            :key="item.id"
                            :label="item.countryCode"
                            :value="item.id">
                        </el-option>
                    </el-select>
                    <el-select v-model="form.currencyTypeId" placeholder="">
                        <el-option
                            v-for="item in currencyTypeIdOptions"
                            :key="item.id"
                            :label="item.symbol"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </div>
            </div>
            <div class="pushitem">
                <div class="title"><b></b> ()</div>
                <div class="solditem clearfix">
                    <el-tabs v-model="activeName"  @tab-click="handleClick">
                        <el-tab-pane label="" :disabled="priceTypeDisabled" name="first">
                            <div class="marketprice">
                                <div class="marketitem">
                                    <h4></h4>
                                    <el-select v-model="Average" placeholder="">
                                        <el-option
                                        v-for="item in Averageoptions"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                        </el-option>
                                </el-select>
                                </div>
                                <div class="marketitem">
                                    <h4></h4>
                                    <span class="yourprice">
                                        {{yourprice}}
                                    </span>
                                </div>
                                <div class="marketitem">
                                <h4></h4>
                                <span class="lowprice">
                                    52
                                </span>
                                </div>
                            </div>
                            <div class="premium">
                                <span class="text">
                                     <i>()</i>
                                </span>
                                <div class="block">
                                    <el-slider
                                        v-model="form.premiumPer"
                                        :format-tooltip="formatTooltip"
                                        :show-tooltip="false"
                                        :show-input="true"
                                        :min="-70"
                                        :max="70"
                                        :debounce="100"
                                        :step="0.001"
                                        >
                                    </el-slider>
                                </div>
                                <div class="premiumtools clearfix">
                                    <div class="formula">
                                        1000 CNY * <span>{{premiumRate}}</span>
                                    </div>
                                    <div class="premiumpricetips">
                                        ()
                                    </div>
                                </div>
                            </div>
                            <div class="acceptlowprice">
                                <h4><span>()</span></h4>
                                <input type="number" v-model="form.lowestPrice" id="acceptlowprice" name="acceptlowprice" placeholder="">
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="" name="second">
                            <div class="regularpricve">
                                <ul class="clearfix">
                                    <li>
                                        <span class="yourprice">
                                            
                                        </span>
                                        <input type="text" v-model="form.price" name="Youroffer" id="Youroffer" placeholder="">
                                    </li>
                                    <li>
                                        <span class="yourprice">
                                            
                                        </span>
                                        <div class="currentlowprice">5823</div>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </div>
            </div>
            <div class="pushitem">
                <div class="title"><b></b> ()</div>
                <div class="paymentlist">
                    <button type="button" v-for="item in paymentlist" @click="CheckPayment($event,item.value)">{{item.displayText}}</button>
                </div>
            </div>
            <div class="pushitem">
                <div class="title"><b></b> ()</div>
                <div class="limititem">
                    <ul class="clearfix">
                        <li>
                            <div class="limittile"><span>()</span></div>
                            <div class="limitpride">
                                <input type="number" v-model="form.mixPrice"  name="limitlowpride" placeholder="">
                                <b class="currency">CNY</b>
                            </div>
                        </li>
                        <li>
                            <div class="limittile"><span>()</span></div>
                            <div class="limitpride">
                                <input type="number" v-model="form.maxPrice"  name="limithighpride" placeholder="">
                                <b class="currency">CNY</b>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="pushitem">
                <div class="title"><b></b> ()</div>
                <div class="transactiondescription">
                    <textarea name="transactiondescription" v-model="form.transDesc" id="transactiondescription" placeholder="115[]
    215[] 
    3"></textarea>
                </div>
            </div>
            <div class="pushitem">
                <div class="title"><b></b> ()</div>
                <div class="transactiondescription">
                    <textarea name="transactionnote" v-model="form.transactionnote" id="transactionnote" placeholder="BTCVC  

    XXXX XXXX XXXX XXXX 
    XXXXXXXXXXX 
    XXXXXXXXXXX 
    "></textarea>
                </div>
            </div>
            <div class="pushitem">
                <div class="showhightoption" @click="isShowhightoption = !isShowhightoption">
                    
                    <i class="el-icon-arrow-down" id="hightoption_arrow"></i>
                </div>
            </div>
            <el-collapse-transition>
                <div class="hightoptionitem" v-if="isShowhightoption">
                    <div class="pushitem">
                        <div class="title"><b></b> </div>
                        <div class="autoitem">
                            <span class="text"><i></i></span>
                            <input type="text" name="greetings" v-model="form.greetings" placeholder="">
                        </div>
                        <div class="autoitem">
                            <span class="text"><i></i></span>
                            <input type="text" v-model="form.conclusion" name="conclusion" placeholder="">
                        </div>
                    </div>
                    <div class="pushitem">
                        <div class="title"><b></b> </div>
                        <div class="autoitem">
                            <span class="text">0=</span>
                            <input type="number" name="maximum" v-model="form.limitMinOrder" value="0">
                        </div>
                        <div class="autoitem">
                            <span class="text"></span>
                            <input type="number" name="dealTime" v-model="form.coProcess" value="0">
                        </div>
                        <div class="autoitem">
                            <el-checkbox v-model="form.limitMustRealAuth"></el-checkbox>
                            <el-checkbox v-model="form.limitMustAdvancedAuth"></el-checkbox>
                            <div class="limitsettips"></div>
                        </div>
                    </div>
                </div>
            </el-collapse-transition>
            <div class="pushitem">
                <div class="title"><b></b></div>
                <div class="precautions">
                    <ul>
                        <li>OTV VC </li>
                        <li></li>
                        <li>OTV VC </li>
                        <li>3-14</li>
                    </ul>
                </div>
            </div>
            <el-form-item>
                <el-button class="btn-color publishbtn" v-if="!isuserlogin" :disabled="!isuserlogin" type="primary" @click="onSubmit()"></el-button>
                <el-button v-else 
                    class="btn-color publishbtn" 
                    :disabled="isPublishNow" 
                    :class="{loading: isPublishNow}" 
                    id="PublishBtnNow" 
                    type="primary" 
                    @click="onSubmit($event)">
                        <b class="text" v-show="!isPublishNow"></b>
                        <i class="el-icon-loading" v-show="isPublishNow" style="font-size:20px"></i>
                </el-button>
            </el-form-item>
            <div class="publishattention">
                <h3></h3>
                <ul>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li>OTV VC </li>
                    <li>OTC VC OTC VC </li>
                </ul>
            </div>
        </el-form>
    </div>
</template>
<script>
export default {
    data(){
        return{
            activeName: 'first',
            priceTypeDisabled: false,
            isPublishNow: false,
            isadstradepage: true,
            isuserlogin: false,
            form: {
                tradeType: 1,
                coinTypeId: 0,
                countryId: 0,
                currencyTypeId: 0,
                priceType: 0,
                premiumPer: 0,
                lowestPrice: 0,
                price: '',
                mixPrice: '',
                maxPrice: '',
                tradeAdsPaymentLists:[],
                transDesc: '',
                // transactionnote: '',
                // greetings:'',
                // conclusion: '',
                limitMinOrder: 0,
                coProcess: 0,
                limitMustRealAuth: true,
                limitMustAdvancedAuth: true,
                limitMustWhitelist: true,
                isPublish: true
            },
            
            tradeTypeOptions: [
                {
                    value: 1,
                    label: ''
                },
                {
                    value: 2,
                    label: ''
                }
            ],
            coinTypeIdOptions: [],
            countryIdOptions: [],
            currencyTypeIdOptions: [],
            Average:'',
            Averageoptions: [
                {
                    value: 1,
                    label: '(Average)'
                }
            ],
            
            
            
            premiumRate: 1,
            originalprice: 1000,
            yourprice: 1000,
            paymentlist: [],
            isShowhightoption: false
            
            
        }
    },
    inject: ['reload'],
    mounted(){
        if(this.isadstradepage){
            document.getElementById('adstradenav').classList.add('active');
        }
        this.isuserlogin = this.commonService.islogin();
        this.getCoinType(this.apiService+'CoinType/GetListAsync');
        this.getCountry(this.apiService+'Country/GetCountry');
        this.getCurrencyType(this.apiService+'CurrencyType/GetCurrencyType');
        this.getAllPaymentMethod(this.apiService+'PaymentMethod/GetAllPaymentMethod');
    },
    methods: {
        handleClick(tab, event) {
            // console.log(event);
        },
        CheckCoinType(val){
            let that = this;
            let Arr = this.coinTypeIdOptions;
            Arr.forEach(function(value, index, Arr){
                if(value.id == val && value.shortName == 'VC'){
                    that.activeName = 'second';
                    that.priceTypeDisabled = true;
                }else{
                    that.priceTypeDisabled = false;
                }
            })
        },
        formatTooltip(val) {
            this.premiumRate = (1 + Number((val/100).toFixed(5))).toFixed(5);
            this.yourprice = (this.originalprice * this.premiumRate).toFixed(2);
            //   this.premiumRate = 1 + val/10;
            console.log(val)
            return val / 10;
        },
        CheckPayment(obj, index){
            if(obj.srcElement.className == 'active'){
                obj.srcElement.classList.remove('active');
                for(let i=0; i<this.form.tradeAdsPaymentLists.length; i++){
                    if(this.form.tradeAdsPaymentLists[i] == index){
                        this.form.tradeAdsPaymentLists.splice(i, 1);
                    }
                }
            }else{
                obj.srcElement.classList.add('active');
                this.form.tradeAdsPaymentLists.push(index);
            }
        },
        onSubmit() {
            let that = this;
            let status = false;
            let PublishInfo = {
                "tradeAds": {}
            };
            if(this.activeName == 'first'){
                this.form.price = this.yourprice;
                this.form.priceType = 0;
            }else{
                this.form.priceType = 1;
                if(!this.form.price){
                    status = true;
                    this.$message('');
                }
            }
            
            if(this.form.tradeAdsPaymentLists.length == 0){
                status = true;
                this.$message('');
            }
            if(!this.form.mixPrice){
                status = true;
                this.$message('');
            }
            if(!this.form.maxPrice){
                status = true;
                this.$message('');
            }
            if(status){
                return false;
            }

            this.isPublishNow = true;

            this.form.userId = this.commonService.TokenCommonSet.getCookie('__userId');
            this.form.tradeAdsPayments = this.form.tradeAdsPaymentLists.join();  
            PublishInfo.tradeAds = this.form;
            let url = this.apiService + 'TradeAds/CreateOrUpdateTradeAds';
            this.$http.post(url,PublishInfo).then(response => {
                console.log(response)
                that.$message({
                    message: '',
                    type: 'success'
                });
                that.isPublishNow = false;
                that.reload();
            },err => {
                console.log(err);
                that.isPublishNow = false;
            });
        },

        getCoinType(url){
            this.$http.get(url).then(res => {
                this.coinTypeIdOptions = res.body.result.items;
                this.form.coinTypeId = res.body.result.items[0].id;
            }, err => {
                console.log(err)
            });
        },

        getCountry(url){
            this.$http.get(url,{"MaxResultCount":300,"SkipCount":0}).then(res => {
                this.countryIdOptions = res.body.result.items;
                this.form.countryId = res.body.result.items[0].id;
            }, err => {
                console.log(err)
            });
        },

        getCurrencyType(url){
            this.$http.get(url,{"MaxResultCount":300,"SkipCount":0}).then(res => {
                console.log(res)
                this.currencyTypeIdOptions = res.body.result.items;
                this.form.currencyTypeId = res.body.result.items[0].id;
            }, err => {
                console.log(err)
            });
        },

        getAllPaymentMethod(url){
            this.$http.get(url).then(res => {
                this.paymentlist = res.body.result;
            }, err => {
                console.log(err)
            });
        }
    }
}
</script>
<style lang="less" scoped>
    .publishpage{
       background: rgba(255,255,2555,0.1);
       padding: 30px 250px;
       margin-top: 30px;
       border-radius: 5px;

       .needlogin{
           margin: -30px -250px 0 -250px;
           background: rgba(12, 31, 44, 0.5);
           padding: 20px 0;
           text-align: center;
           margin-bottom: 20px;
           border-radius: 3px 3px 0 0;
           color: #017EFF
       }

       h1.title{
           background: #017EFF;
           font-size: 24px;
           text-align: center;
           font-weight: normal;
           padding: 10px 0;
           border-radius: 3px;
       }
       .publishtips{
            background: rgba(255,255,2555,0.1);
            padding: 20px;
            line-height: 2;
       }

       .pushitem{
           margin-top: 30px;
           .title{
               color: #017EFF;
               margin-bottom: 25px;
           
                b{
                    font-size: 20px;
                    margin-right: 10px;
                    color: #ffffff;
                    font-weight: normal;
                    position: relative;
                    padding-bottom: 5px;
                }
                b:before{
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    display: block;
                    content: "";
                    background: #017EFF;
                    height: 2px;
                }
           }
            
       }

       .marketprice{
           display: flex;

            .marketitem{
               float: left;
               width: 25%;
                padding-top: 10px;
               h4{
                   font-weight: normal;
                   padding-bottom: 15px;
               }

               .yourprice{
                   display: inline-block;
                   background: #017EFF;
                   border-radius: 3px;
                   line-height: 40px;
                   padding: 0 20px;
                   font-size: 18px;
                   min-width: 60px;
               }
               .lowprice{
                   display: block;
                   font-size: 18px;
                   color: #ffffff;
                   line-height: 40px;
               }
           }
           .marketitem:first-child{
               width: 40%;
               padding-right: 10%;
           }
       }

       .premium{
           padding-top: 20px;
           padding-left: 20px;
           
           .text{
               i{
                   color: #999999;
                   font-size: 12px;
               }
           }

            .premiumtools{
                padding-top: 10px;
                display: flex;

                .formula{
                    margin-right: 30px;
                    text-align: center;
                    float: left;
                    flex: 1;
                    color: #999999
                }
                .premiumpricetips{
                    float: right;
                    width: 250px;
                    text-align: center;
                    color: #999999
                }
            }
       }

       .acceptlowprice{
           padding-top: 20px;
           h4{
               font-weight: normal;
               padding-bottom: 10px;

               span{
                   color: #999999;
               }
           }
           input{
                border: #017EFF solid 1px;
                border-radius: 3px;
                color: #ffffff;
                padding: 10px;
                background: transparent;
                width: 300px;
            }
       }

       .regularpricve{
           li{
               float: left;
               width: 30%;

               span{
                   display: block;
                   padding: 10px 0;
               }

               input{
                     border: #017EFF solid 1px;
                    border-radius: 3px;
                    color: #ffffff;
                    padding: 10px;
                    background: #017EFF;
                    width: 200px;
               }
               ::-webkit-input-placeholder {
                    color: #ccc;
                }
                :-moz-placeholder {
                    color: #ccc;
                }
                ::-moz-placeholder {
                    color: #ccc;
                }
                :-ms-input-placeholder {
                    color: #ccc;
                }
                .currentlowprice{
                    padding: 11px 0;
                    font-size: 18px;
                }
           }
       }

       .paymentlist{
           button{
               padding: 10px 20px;
               border-radius: 3px;
               background: transparent;
               color: #ffffff;
               margin-right: 20px;
               border:solid 1px #017EFF;
               cursor: pointer;
           }
           button.active{
               background: #017EFF;
               color: #ffffff
           }
       }

       .limititem{
           li{
               float: left;
               width: 40%;
               padding-right: 10%;

               .limittile{
                   padding-bottom: 10px;
                   span{
                       color: #999999;
                       padding-left: 3px;
                       font-size: 12px;
                   }
               }

               .limitpride{
                   position: relative;
                   border:solid 1px #017EFF;
                   padding:0;
                   border-radius: 3px;

                   input{
                       background: transparent;
                       width: 100%;
                       padding:15px 50px 15px 15px;
                       color: #ffffff;
                   }

                   .currency{
                       position: absolute;
                       top: 0;
                       right:10px;
                       height: 16px;
                       line-height: 16px;
                       margin: auto;
                       bottom: 0;
                       font-weight: normal;
                   }
               }
           }
       }

       .transactiondescription{
           textarea{
                width: 100%;
                background: rgba(12, 31, 44, 0.4);
                padding:15px 20px;
                border-radius: 3px;
                color: #ffffff;
                line-height: 2;
                height: 200px;
                font-family: Arial, Helvetica, sans-serif
           }
       }

       .showhightoption{
           width: 200px;
           border-radius: 3px;
           border:solid 1px #017EFF;
           padding: 15px 0;
           margin: 0 auto;
           position: relative;
           text-align: center;
           cursor: pointer;

           i{
               position: absolute;
               top: 0;
               right: 10px;
               bottom: 0;
               margin: auto;
               height: 14px;
               transition: all 0.3s
           }
       }

       .showhightoption:hover i{
            transform:rotate(180deg);
            -ms-transform:rotate(180deg); 
            -moz-transform:rotate(180deg);
            -webkit-transform:rotate(180deg);
            -o-transform:rotate(180deg);
            transition: all 0.3s
       }

       .hightoptionitem{
           .autoitem{
               padding-bottom: 15px;

               .text{
                   display: block;
                   padding-bottom: 10px;

                    i{
                        color:#999999;
                        font-size: 12px;
                    }
               }    
                input{
                    background: transparent;
                    padding:15px 10px;
                    border-radius: 3px;
                    border:solid 1px #017EFF;
                    color: #ffffff;
                    width: 100%;
                }
           }
       }

       .limitsettips{
           padding-top: 20px;
       }
       .precautions{
            li{
                padding: 5px 0 5px 20px;
                position: relative;
                line-height: 1.5
            }
            li:before{
                position: absolute;
                top: 10px;
                width: 10px;
                height: 10px;
                background: #017EFF;
                left: 0;
                display: block;
                content: "";
                border-radius: 50%;
            }
            li:last-child{
                color: #017EFF;
            }

        }

        .publishbtn{
            margin: 20px auto;
            display: block;
            width: 200px;
            padding: 15px 0;
            font-size: 16px;
            
            b{
                font-weight: normal
            }
       }
       
       .publishattention{

           h3{
               font-size: 20px;
               font-weight: normal;
               color: #017EFF;
               padding-bottom: 10px;
           }

           li{
               padding: 5px 0 5px 10px;
               position: relative;
               line-height: 1.5;
           }
           li:before{
               position: absolute;
               top: 13px;
               left: 0;
               width: 4px;
               height: 4px;
               background: #ffffff;
               border-radius: 50%;
               display: block;
               content: ''
           }
       }
    }

    @media only screen and (min-width: 320px) and (max-width:768px){
        .publishpage{
            padding: 0;
            margin-left: 15px;
            margin-right: 15px;
            margin-top: 10px;
        }
        .publishpage .pushitem{
            padding-left: 15px;
            padding-right: 15px;
        }

        .publishpage .marketprice{
            display:block;
        }
        .publishpage .marketprice .marketitem,
        .publishpage .marketprice .marketitem:first-child{
            width: 100%;
            padding-right: 0;
            float: none;
            padding-top: 15px;
        }
        .publishpage .marketprice .marketitem h4{
            padding-bottom: 5px;
        }
        .publishpage .marketprice .marketitem .lowprice{
            line-height: 1;
        }
        .publishpage .premium{
            padding-left: 0;
        }
        .publishpage .premium .premiumtools{
            display: block;
        }
        .publishpage .premium .premiumtools .formula{
            flex: auto;
            text-align: center;
            float: none;
        }
        .publishpage .premium .premiumtools .premiumpricetips{
            float: none;
            width: 100%;
            padding-top: 10px;
        }
        .publishpage .paymentlist button{
            margin-bottom: 10px;
        }
        .publishpage .limititem li{
            float: none;
            width: 100%;
            padding-right: 0;
            padding-bottom: 15px;
        }
        .publishpage .limititem li:last-child{
            padding-bottom: 0
        }
        .publishattention{
            padding: 0 15px 10px;
        }
        .publishpage .regularpricve li{
            float: none;
            width: 100%;
            padding-bottom: 10px;
        }
        .publishpage .regularpricve li .currentlowprice{
            padding: 0;
        }
        .publishpage .needlogin{
            margin: 0;
        }
    }
</style>



